import React from 'react';
import Layout from '../components/layout';

export default function BlogLayout({ data: { mdx } }) {
  return (
    <Layout>
      <div>{mdx.frontmatter.title}</div>
    </Layout>
  );
}

export const pageQuery = graphql`
  query BlogPostQuery($id: String) {
    mdx(id: { eq: $id }) {
      id
      body
      frontmatter {
        title
      }
    }
  }
`;
